import "../../components/development/development-all.scss";
import "./contact.scss";
import { Link as MyLink} from "react-router-dom";

const Contact = (props) => {
  return (
    <div className="contact">
      <hr></hr>
      <div className="container">
        <div className="call">
          <MyLink
            to="viber://chat/?number=+380977454521"
            style={{ color: "gray" }}
            className="contact-btn"
          >
            <img
              src={require("../../resource/viber.png")}
              className="viber-img"
              alt="viber"
            ></img>
            <br></br>Написати у Viber
          </MyLink>
          <MyLink
            to="https://telegram.me/julasweta"
            style={{ color: "gray" }}
            className="contact-btn"
          >
            <img
              src={require("../../resource/telegram.png")}
              className="viber-img"
              alt="viber"
            ></img>
            <br></br>Написати у Telegram
          </MyLink>
          <MyLink to="/modal">
            <button className="order">Замовити дзвінок</button>
          </MyLink>
        </div>
      </div>
    </div>
  );
};

export default Contact;
